.layout {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* background-color: lightblue; */
}

.layout .image {
  width: 25vw;
  aspect-ratio: 1/1;
  /* background-color: palevioletred; */
  /* overflow: hidden; */
}

.layout .subHeader {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.layout .subHeader .dashboard {
  border-radius: 0.5vw;
  font-size: 1.25vw;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  margin: 1vh 1vw 1vh 1vw;
  padding: 0.5vw;
  background-color: var(--header-color);
  color: #edf2f4;
  cursor: pointer;
  display: inline-block;
  transition-duration: 0.5s;
}

.layout .subHeader .dashboard:hover,
.layout .subHeader .dashboard:focus {
  background-color: var(--main-menu);
  /* color: #000000; */
  padding-right: 2vw;
}

.layout .subHeader .dashboard span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.layout .subHeader .dashboard span:after {
  content: "\27A5";
  /* content: "\22D9"; */
  position: absolute;
  opacity: 0;
  top: 0;
  right: 0.5vw;
  transition: 0.5s;
}

.layout .subHeader .dashboard:hover span:after,
.layout .subHeader .dashboard:focus span:after {
  opacity: 1;
  right: -1.5vw;
}
