.container {
  width: 50vw;
  height: 60vh;
  border-radius: 1vw;
  background-color: var(--popup-background);
  z-index: 11;
  position: fixed;
  top: 20vh;
  left: 25vw;
  display: block;
}

.container .symbolDone {
  position: relative;
  top: -8vh;
  left: 43%;
  width: 7.5vw;
  aspect-ratio: 1/1;

  background-color: var(--warn-color);
  color: white;

  border-radius: 50%;
  border: 0.4vw solid var(--popup-background);
  padding: 0.5vw;

  display: grid;
  place-content: center;

  animation: done 2s infinite;
}

.container .symbolWarn {
  position: relative;
  top: -8vh;
  left: 43%;
  width: 7.5vw;
  aspect-ratio: 1/1;

  background-color: var(--warn-color);
  color: white;

  border-radius: 50%;
  border: 0.4vw solid var(--popup-background);
  padding: 0.5vw;

  display: grid;
  place-content: center;

  animation: warning 1.5s infinite;
}

.container .symbolCritical {
  position: relative;
  top: -8vh;
  left: 43%;
  width: 7.5vw;
  aspect-ratio: 1/1;

  background-color: var(--warn-color);
  color: white;

  border-radius: 50%;
  border: 0.4vw solid var(--popup-background);
  padding: 0.5vw;

  display: grid;
  place-content: center;

  animation: critical 0.75s infinite;
}

.container .content {
  height: 86%;
  position: relative;
  top: -8vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  /* border: 1px solid red; */
}

.container .content .info {
  flex-basis: 70%;
  width: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1vw 4vw;
  font-size: 2vw;
  font-weight: bold;
  flex-wrap: wrap;
  overflow: auto;
  /* background-color: rgb(255, 0, 255, 0.15); */
}

.container .content .info::-webkit-scrollbar {
  display: none;
}

.container .content .input {
  flex: 10%;
  width: 80%;
  display: block;
  /* background-color: rgb(255, 69, 0, 0.25); */
}

.container .content .buttons {
  flex: 20%;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  column-gap: 1vw;
  /* background-color: rgb(0, 255, 255, 0.25); */
}

@keyframes done {
  0% {
    /* background-color: var(--good-color); */
    background-color: var(--popup-background);
  }
  10% {
    background-color: var(--good-color);
  }
  80% {
    background-color: var(--good-color);
  }
  100% {
    /* background-color: var(--good-color); */
    background-color: var(--popup-background);
  }
}

@keyframes warning {
  0% {
    background-color: var(--popup-background);
    /* background-color: var(--warn-color); */
  }
  10% {
    background-color: var(--warn-color);
  }
  80% {
    background-color: var(--warn-color);
  }
  100% {
    /* background-color: var(--warn-color); */
    background-color: var(--popup-background);
  }
}

@keyframes critical {
  0% {
    /* background-color: var(--bad-color); */
    background-color: var(--popup-background);
  }
  10% {
    background-color: var(--bad-color);
  }
  80% {
    background-color: var(--bad-color);
  }
  100% {
    /* background-color: var(--bad-color); */
    background-color: var(--popup-background);
  }
}
