.container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

h2 {
  text-align: center;
  text-transform: uppercase;
}

.container .content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.container .content .nav {
  flex-basis: 7%;
  display: flex;
  flex-direction: row;
  column-gap: 1vw;
  padding-bottom: 0.5vw;
  justify-content: space-between;
  align-items: center;
  /* background-color: lawngreen; */
}

.container .content .nav .filter {
  flex: 75%;
  display: flex;
  flex-direction: row;
  width: 35%;
}

.container .content .nav .navBut {
  flex: 25%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  column-gap: 1vw;
  /* background-color: powderblue; */
}

.container .content .table {
  flex: 93%;
  height: 100%;
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  /* background-color: aqua; */
}

.container .content .table .tabView {
  overflow: scroll;
  height: 93%;
}

.container .content .table .tabView::-webkit-scrollbar {
  display: none;
}

.popContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  margin-top: 0.5vw;
  row-gap: 0.5vw;
  /* background-color: aqua; */
}

.popContainer .row {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.popContainer .row .title {
  /* background-color: magenta; */
  flex-basis: 20vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  text-transform: capitalize;
}

.popContainer .row .input {
  /* background-color: lawngreen; */
  flex: 1;
  display: flex;
  flex-direction: row;
  column-gap: 0.5vw;
  justify-content: center;
  align-items: center;
}

.buttons {
  margin-top: 1vw;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  column-gap: 1vw;
  /* background-color: yellow; */
}
