:root {
  /* --header-color: rgb(27, 33, 51); */
  --header-color: #160f29;
  --gradient-background: linear-gradient(
    to top right,
    var(--header-color) 50%,
    #246a73
  );
  --gradient-header: linear-gradient(
    to bottom right,
    #160f29,
    #004346,
    #160f29
  );
  /* --hover-header: rgb(12, 18, 36); */
  --hover-header: rgb(82, 87, 94);
  --body-backgroud: #ddbea8;
  --content-background: rgba(242, 246, 253, 0.681);
  --popup-background: rgb(254, 246, 255);
  --popup2-background: rgb(206, 206, 206);
  --light-text: rgb(255, 255, 255);
  /* --dark-text: rgb(63, 72, 101); */
  --dark-text: rgb(71, 71, 71);
  --shadow-color: rgba(0, 0, 0, 0.25);
  --border-color: rgb(151, 151, 151);
  --main-menu: #368f8b;
  --sub-memu: #f3dfc1;
  --button-on: #29bf12;
  --button-off: #c1292e;
  --good-color: rgb(41, 191, 18);
  --warn-color: rgb(247, 127, 0);
  --bad-color: rgb(193, 41, 46);

  --gradient-good: linear-gradient(
    to top right,
    #004b2c,
    var(--button-on),
    #004b2c
  );

  --gradient-warning: linear-gradient(
    to top right,
    var(--warn-color),
    #f5d628,
    var(--warn-color)
  );

  --gradient-critical: linear-gradient(
    to top right,
    #240000,
    var(--button-off),
    #240000
  );
}
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

@page {
  size: A4;
  /* size: 216mm 280mm; */
  margin: 5mm 10mm 5mm 10mm;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--body-backgroud);
  /* background-image: var(--gradient-header); */
  /* background-image: url("./image/wall2.jpg"); */
}

@media only screen and (max-width: 1080px) {
  h1 {
    font-size: 3vh;
    text-transform: uppercase;
    text-align: center;
  }

  h2 {
    font-size: 2.5vh;
    text-transform: capitalize;
  }

  h3 {
    font-size: 2vh;
    font-weight: 600;
    text-transform: capitalize;
  }

  h4 {
    font-size: 1.5vh;
    font-weight: normal;
    text-transform: capitalize;
  }

  .App {
    position: fixed;
    top: 0;
    left: 0;
  }

  .App .navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1;
    /* background-color: palevioletred; */
  }

  .App .container {
    position: fixed;
    top: 4vh;
    left: 0;
    /* background-color: magenta; */
  }

  .App .container #main {
    left: 0;
    width: 97vw;
    height: 85.5vh;
    margin: 0 1.5vw 1vh 1.5vw;
    padding: 0.5vh;
  }

  .App .container #main .container {
    width: 95vw;
    height: 85vh;
    border-radius: 1.5vh;
  }

  .App .container #foot {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100vw;
    height: 4vh;
    font-size: 1.25vh;
    /* background-color: gray; */
  }

  .table th {
    font-size: 1.5vh;
    word-wrap: break-word;
    text-align: center;
  }

  .table td {
    font-size: 1.2vh;
    word-wrap: break-word;
    text-align: center;
  }

  .table td input {
    font-size: 1vh;
  }

  table th {
    padding: 0.5vh 1vh;
  }

  table td {
    padding: 0.25vh 1vh;
  }
}

@media only screen and (min-width: 1081px) {
  h1 {
    font-size: 3vw;
    text-transform: uppercase;
    text-align: center;
  }

  h2 {
    font-size: 1.75vw;
    text-transform: capitalize;
  }

  h3 {
    font-size: 1vw;
    font-weight: 600;
    text-transform: capitalize;
  }

  h4 {
    font-size: 1vw;
    font-weight: normal;
    text-transform: capitalize;
  }

  .App {
    position: fixed;
    top: 0;
    left: 0;
  }

  .App .navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: auto;
    z-index: 12;
  }

  .App .container {
    position: fixed;
    top: 0;
    left: 3vw;
  }
  .App .container #main {
    width: 95vw;
    height: 95vh;
    margin: 1vh 1vw 1vh 1vw;
    padding: 0.5vw;
  }

  .App .container #main .container {
    width: 95vw;
    height: 95vh;
    border-radius: 1.5vw;
  }

  .App .container #foot {
    position: fixed;
    left: 3vw;
    bottom: 0;
    width: 97vw;
    height: 4vh;
    font-size: 0.9vw;
  }

  .table th {
    font-size: 1.1vw;
    word-wrap: break-word;
    text-align: center;
  }

  .table td {
    font-size: 1vw;
    word-wrap: break-word;
    text-align: center;
  }

  .table td input {
    font-size: 1vw;
  }

  table th {
    padding: 0.5vw 1vw;
  }

  table td {
    padding: 0.25vw 1vw;
  }
}

.App {
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  z-index: -2;
  overflow-y: none;
}

.App::-webkit-scrollbar {
  display: none;
}

.App .container {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  z-index: -1;
}

.App .container #main {
  z-index: 0;
  /* background-color: #2196f3; */
}

input {
  width: 100%;
  border: none;
  border-radius: 1vw;
  padding: 0.25vw 1vw;
  background-color: khaki;
}

select {
  width: 100%;
  border: none;
  background-color: khaki;
  padding: 0.2vw 1vw;
  border-radius: 1vw;
}

select option {
  background-color: white;
}

.App .container #main .container {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  background-color: var(--content-background);
  /* background-color: magenta; */
  margin: 0;
  padding: 1vh 2vw;
  z-index: 1;
  position: inherit;
  /* display: none; */
  /* cursor: pointer; */
}

.App .container #foot {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  z-index: 0;
  text-transform: capitalize;
}

.App .container #foot span {
  font-weight: bold;
}

.gaugeInfoLabel {
  display: flex;
  justify-content: space-between;
  color: #ffffff;
  text-align: center;
}

.table {
  border-collapse: collapse;
  text-align: center;
  width: 100%;
  position: inherit;
}

.table thead {
  position: sticky;
  top: 0;
  /* border-radius: 0.9vw 0.9vw 0 0; */
  background-color: var(--header-color);
  background-image: var(--gradient-header);
  color: var(--light-text);
  font-weight: bold;
  text-transform: uppercase;
}

.table tbody {
  color: var(--hover-header);
}

.table tbody tr:nth-of-type(even) {
  background-color: rgb(210, 214, 222);
}

.table tbody tr:nth-of-type(odd) {
  background-color: var(--content-background);
}

.table tbody tr:last-of-type {
  border-bottom: 0.2vw solid var(--header-color);
}

/* .table tbody tr:hover {
  font-weight: bold;
  border: 2px solid var(--hover-header);
} */

.subTable {
  border-collapse: collapse;
  text-align: center;
  width: 100%;
  border: 2px solid black;
}

.subTable thead {
  position: sticky;
  top: 0;
  /* border-radius: 0.9vw 0.9vw 0 0; */
  background-color: var(--header-color);
  color: var(--light-text);
  font-weight: bold;
}

.subTable th {
  border-right: 2px solid black;
  border-left: 2px solid black;
  border-bottom: 2px solid black;
}

.subTable tbody td {
  border-right: 2px solid black;
  border-left: 2px solid black;
}

.subTable tbody tr {
  border-bottom: 0.25px solid grey;
}

