.subTable tbody tr:hover {
  /* font-weight: bold; */
  background-color: var(--hover-header);
  color: var(--light-text);
}

.button {
  background-color: #8d99ae;
  border: 0.15vw solid #2b2d42;
  border-radius: 0.5vw;
  /* box-shadow: 0 5px #999; */
  color: #2b2d42;
  cursor: pointer;
  display: inline-block;
  font-size: 1vw;
  /* margin: 1vw 1vw; */
  padding: 0.5vw 1.25vw;
  text-align: center;
  text-decoration: none;
  transition-duration: 0.5s;
  /* box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
}

.button:hover,
.button:focus {
  background-color: #2b2d42;
  color: #edf2f4;
}

.button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.button span:after {
  content: "\27A5";
  /* content: '\22D9'; */
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

.button:hover span {
  padding-right: 1.25vw;
}

.button:hover span:after {
  opacity: 1;
  right: 0;
}

.button:active {
  background-color: #2b2d42;
  box-shadow: 0 1px #666;
  transform: translateY(4px);
}

.delButton {
  position: relative;
  width: 8vw;
  height: 4vw;
  background-color: #333;
  border-radius: 0.5vw;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.5s;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  text-decoration: none;
}

.delButton.active {
  background-color: #2196f3;
}

.delButton span {
  position: absolute;
  top: 2vh;
  left: 1.1vw;
  width: 1.5vw;
  height: 4vh;
  display: inline-block;
  background-color: #fff;
  border-radius: 0 0 0.25vw 0.25vw;
  transition: 0.5s;
}

.delButton:hover span {
  transform: scale(1.2) rotate(60deg) translateY(1.5vw);
}

.delButton.active span {
  left: 50%;
  transform: translateX(-50%) rotate(-45deg);
  border-radius: 0;
  width: 20px;
  height: 10px;
  background: transparent;
  border-left: 2px solid #fff;
  border-bottom: 2px solid #fff;
}

.delButton span::before {
  content: "";
  position: absolute;
  top: -0.75vh;
  left: 0;
  width: 100%;
  height: 0.5vh;
  background-color: #fff;
  box-shadow: 1vw -0.4vh 0 #333, 1vw -0.6vh #333, 1.4vw -0.2vh #333,
    0.5vw -0.35vh 0 #fff;
  transition: 0.5px;
}

.delButton.active:hover span::before,
.delButton.active span::before {
  transform: scale(0);
}

.delButton:hover span::before {
  transform: rotate(-90deg) translateX(50%) translateY(-1vw);
}

.delButton text {
  position: absolute;
  color: #fff;
  right: 1.2vw;
  font-size: 1vw;
  transition: 0.5s;
}

.delButton:hover text,
.delButton.active text {
  transform: translateX(-4vw) translateY(-0.2vw) scale(0);
}

.primaryButton {
  background-color: var(--header-color);
  color: var(--light-text);
  padding: 1vw 2vw;
  border: none;
  border-radius: 0.5vw;
  text-transform: uppercase;
  font-weight: bold;
  cursor: pointer;
}

.primaryButton:hover {
  background-color: var(--hover-header);
}

.secondaryButton {
  background-color: transparent;
  color: var(--header-header);
  padding: 0.8vw 1.8vw;
  border: 0.2vw solid var(--header-color);
  border-radius: 0.5vw;
  text-transform: uppercase;
  font-weight: bold;
  cursor: pointer;
}

.secondaryButton:hover,
.secondaryButton:focus {
  background-color: lightgray;
}

.tertiaryButton {
  background-color: var(--button-off);
  color: var(--light-text);
  padding: 1vw 2vw;
  border: none;
  border-radius: 0.5vw;
  text-transform: uppercase;
  font-weight: bold;
  cursor: pointer;
}

.tertiaryButton:hover,
.tertiaryButton:focus {
  background-color: lightcoral;
}

.toggleButton {
  background-color: var(--button-off);
  aspect-ratio: 1/1;
  width: 3.75vw;
  border: none;
  border-radius: 0.5vw;
  font-weight: bold;
  color: var(--light-text);
  transition: 0.5s;
}

@supports (-webkit-backdrop-filter: none) or (backdrop-filter: none) {
  .backdrop {
    position: fixed;
    background-color: rgb(0, 0, 0, 0.75);
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    transition: 1s;
  }
}

#main .loading {
  display: grid;
  place-content: center;
  width: 95vw;
  height: 95vh;
  border-radius: 1.5vw;
  background-color: var(--content-background);
  z-index: 2;
  position: fixed;
  top: 1vh;
  /* display: none; */
}

#main .loading::after {
  content: "";
  width: 6vw;
  height: 6vw;
  border: 2vw solid var(--header-color);
  border-top-color: var(--main-menu);
  border-radius: 50%;
  animation: load 0.75s linear infinite;
}

@keyframes load {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
