.box {
  background-color: rgb(86, 86, 86);
  padding: 15px;
  border-radius: 10px;
  color: #ffff;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: center;
  font-size: 10px;
  font-family: sans-serif;
  -ms-transform: skew(170deg); /* Support untuk IE 9 */
  -webkit-transform: skew(170deg); /* support untuk Safari */
  transform: skew(170deg); /* default syntax */
  height: 120px;
  width: 100%;
}

.modalPilih {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border-radius: 0.5vw;
  background-color: var(--popup-background);
  padding: 1vw;
  width: 60%;
  max-height: 70%;
  position: fixed;
  top: 15%;
  left: 20%;
  z-index: 21;
}

.modalpilih .tabView {
  overflow: scroll;
  height: 95%;
  width: 95%;
}

.modalpilih .tabView::-webkit-scrollbar {
  display: none;
}

.modalKeyboardNumeric {
  /* background-color: mediumvioletred; */
  width: 30%;
  position: fixed;
  bottom: 5vh;
  left: 35%;
  z-index: 22;
  font-size: 2vw;
}

.modalKeyboard {
  /* background-color: mediumvioletred; */
  width: 70%;
  position: fixed;
  bottom: 5vh;
  left: 15%;
  z-index: 22;
  font-size: 2vw;
}

.modalKeyboard .buttonKey {
  padding: 5vw 0.5vw;
  background-color: aqua;
}

.modaltable {
  display: flex;
  flex-direction: column;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  background-color: var(--popup-background);
  /* padding: 2rem; */
  text-align: left;
  width: 100%;
  overflow: auto;
  top: 5vh;
  height: 80%;
  left: 20%;
  z-index: 22;
}

.modal {
  display: flex;
  flex-direction: column;
  border-radius: 0.75vw;
  background-color: var(--popup-background);
  padding: 2rem;
  text-align: left;
  width: 80%;
  max-height: 90%;
  position: fixed;
  overflow: auto;
  top: 1%;
  left: 10%;
  margin-left: auto;
  margin-right: auto;
  z-index: 20;
}

.modaldetail {
  display: flex;
  flex-direction: column;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  background-color: var(--popup-background);
  /* padding: 2rem; */
  width: 100%;
  overflow: auto;
  top: 5vh;
  height: 80%;
  left: 20%;
  z-index: 3;
}

.modal .buttons {
  display: flex;
  flex-direction: row;
  justify-content: center;
  column-gap: 1vw;
}

.modal .input {
  width: 75%;
  height: 1vw;
}

.modal .input input {
  width: 100%;
  font-size: 1vw;
}
.modal .title {
  width: 25%;
  height: 1vw;
  font-size: 1vw;
}

.modalpilih .form {
  /* background-color: aqua; */
  width: 70%;
}
